@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
:root {
  --red-principal: #E86060;
  --dark-red-secondary: #662323;
  --soft-gray: #BDBDBD;
  --gray: #828282;
  --light-red: #FF7676;
}

* {
  font-family: 'Quicksand', sans-serif;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*::-webkit-scrollbar {
  width: 0.5vw;
}

*::-webkit-scrollbar-thumb {
  background: #E86060;
  background: var(--red-principal);
  border-radius: 0.75rem;
}

*::-webkit-scrollbar-track {
  background: #662323;
  background: var(--dark-red-secondary);
  border-radius: 0.75rem;
}

.Cover{margin-bottom:2rem;height:15.625rem;display:flex}.Cover__img{width:30%;height:100%}.Cover__img img{width:250px;height:250px;object-fit:cover}.Cover__content{width:70%;height:100%;padding:1rem;box-sizing:border-box;display:flex}.Cover__content h3{font-weight:700;font-size:1.375rem}.Cover__content p span{color:var(--dark-red-secondary);margin-left:0.5rem}.Cover__details h3{color:#ffffff}.Cover__details p{color:#ffffff;font-size:0.75rem}.Cover__details p:first-child{font-size:0.875rem}.Cover__background{position:fixed;width:81%;max-width:none;max-width:initial;height:15.625rem;background-color:var(--red-principal);opacity:0.5;z-index:-1}.Cover__btn-filled,.Cover__btn-outline{border:none;border-radius:6.25rem;width:7.313rem;height:1.938rem;margin-right:1rem;cursor:pointer;transition:all 0.15s}.Cover__btn-filled:hover,.Cover__btn-outline:hover{font-weight:bold}.Cover__btn-filled{background-color:var(--red-principal);color:#ffffff;margin-top:3rem}.Cover__btn-outline{background-color:#ffffff;border:1px solid var(--red-principal);color:var(--red-principal);background-color:transparent}

.AlbumCard{width:10rem;height:15rem;transition:all 0.25s}.AlbumCard:hover{-webkit-transform:translatey(-5px);transform:translatey(-5px);box-shadow:0 3px 4px rgba(0,0,0,0.2);border-radius:0.25rem}.AlbumCard img{width:10rem;height:10rem;margin-bottom:0.5rem;cursor:pointer;transition:all 0.25s;border-radius:0.2rem}.AlbumCard img:hover{width:10.25rem;height:10.25rem;border-radius:0.2rem;-webkit-transform:translate(-2px);transform:translate(-2px);box-shadow:0 3px 4px rgba(0,0,0,0.2)}.AlbumCard__album{font-weight:700;font-size:0.875rem;color:var(--gray);text-decoration:none;display:block;margin-left:0.25rem;transition:all 0.15s}.AlbumCard__album:hover{font-size:0.9rem}.AlbumCard__artist{font-size:0.75rem;color:var(--soft-gray);text-decoration:none;display:block;margin-left:0.25rem;transition:all 0.15s}.AlbumCard__artist:hover{font-weight:bold}

.Results h3{font-size:1.375rem;font-weight:700;color:var(--red-principal)}.Results__container{display:grid;grid-gap:1rem;gap:1rem;grid-auto-rows:15rem;grid-template-columns:repeat(auto-fill, minmax(15rem, 1fr));overflow-y:scroll;max-height:45vh}.Results__loader{display:inline-block;position:relative;width:80px;height:80px}.Results .hidden{display:none}.Results__loader div{box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid var(--red-principal);border-radius:50%;-webkit-animation:loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:var(--red-principal) transparent transparent transparent}.Results__loader div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.Results__loader div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.Results__loader div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes loader{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes loader{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.AudioPlayer{display:flex;justify-content:space-between;bottom:0;position:absolute;width:100vw;height:6.25rem;background-color:var(--red-principal);overflow:hidden}.AudioPlayer__cover{display:flex;align-items:center}.AudioPlayer__cover img{width:6.25rem;height:6.25rem;margin:0.2rem 1rem 0 0;object-fit:cover}.AudioPlayer__cover p{font-size:0.75rem;color:#ffffff}.AudioPlayer__cover p:first-child{font-size:0.875rem}.AudioPlayer__cover p:hover{cursor:pointer}.AudioPlayer__buttons{display:flex;align-items:center;grid-gap:1rem;gap:1rem}.AudioPlayer__buttons .fa-step-backward{font-size:2rem;color:#ffffff;cursor:pointer;transition:all 0.15s}.AudioPlayer__buttons .fa-step-backward:hover{font-size:2.25rem}.AudioPlayer__buttons .fa-play-circle,.AudioPlayer__buttons .fa-pause-circle{font-size:3.75rem;color:#ffffff;cursor:pointer;transition:all 0.15s}.AudioPlayer__buttons .fa-play-circle:hover,.AudioPlayer__buttons .fa-pause-circle:hover{font-size:4rem}.AudioPlayer__buttons .fa-step-forward{font-size:2rem;color:#ffffff;cursor:pointer;transition:all 0.15s}.AudioPlayer__buttons .fa-step-forward:hover{font-size:2.25rem}.AudioPlayer__vol{display:flex;align-items:center;margin-right:3rem}.AudioPlayer__vol input{cursor:pointer;margin-right:0.5rem}.AudioPlayer__vol input[type='range']{overflow:hidden;width:8rem;-webkit-appearance:none;background-color:gray;border-radius:5rem}.AudioPlayer__vol input[type='range']::-webkit-slider-thumb{width:10px;-webkit-appearance:none;height:10px;background:#ffffff;box-shadow:-80px 0 0 80px #ffffff;border-radius:1rem}.AudioPlayer__vol .fa-volume-off,.AudioPlayer__vol .fa-volume-down,.AudioPlayer__vol .fa-volume-up{font-size:2rem;color:#ffffff}

.Search{width:500px;margin:1rem 0}.Search__box{position:relative;width:500px;height:2.25rem}.Search__input{position:absolute;top:0;left:0;width:100%;padding:10px 20px;border-radius:6.25rem;font-size:1.125rem;border-width:0.05rem}.Search__btn{position:absolute;top:0;right:0;width:60px;z-index:1;cursor:pointer}.Search__btn .fas{position:absolute;right:0;padding:0;-webkit-transform:translate(20px, 13px);transform:translate(20px, 13px);color:var(--gray)}

.Header{max-height:5vh;margin-bottom:2rem;display:flex;justify-content:space-between;align-items:center}.Header__user{cursor:pointer}.Header__user h5{font-size:1.25rem}.Header__user h5 span{margin-right:1rem}.Header__user h5 span i{color:var(--red-principal)}

.Sidebar{position:absolute;width:20.625rem;height:100%;background:var(--dark-red-secondary);box-shadow:0px 4px 4px rgba(0,0,0,0.25)}.Sidebar__logo{padding:2rem 1rem 1rem 2rem;width:250px;height:59px}ul{list-style:none;text-decoration:none;color:#ffffff;padding:0 2rem 0 2rem}.Sidebar__title{font-size:1.375rem;padding-bottom:2rem;font-weight:bold}.Sidebar__link{text-decoration:none;color:#ffffff;font-size:1rem;transition:all 0.15s;font-weight:normal}.Sidebar__link:hover{font-weight:bold}

.Content{margin-left:20.625rem;padding:1rem}

