.Results {
  h3 {
    font-size: 1.375rem;
    font-weight: 700;
    color: var(--red-principal);
  }
  &__container {
    display: grid;
    gap: 1rem;
    grid-auto-rows: 15rem;
    grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
    overflow-y: scroll;
    max-height: 45vh;
  }
  &__loader {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .hidden {
    display: none;
  }
  &__loader div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid var(--red-principal);
    border-radius: 50%;
    animation: loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: var(--red-principal) transparent transparent transparent;
  }
  &__loader div:nth-child(1) {
    animation-delay: -0.45s;
  }
  &__loader div:nth-child(2) {
    animation-delay: -0.3s;
  }
  &__loader div:nth-child(3) {
    animation-delay: -0.15s;
  }
  @keyframes loader {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}